import React,{useEffect,useState} from 'react'

export const Covid = () => {

    const [sata, setsata] = useState([]);

    const getCovidData =async ()=>{
        try{
            const res =await fetch('https://api.covid19india.org/data.json');
            const data= await res.json();
            console.log(data.statewise[0]);
            setsata(data.statewise[0]);
        }catch(err){
            console.log(err);
        }
        
    }

    useEffect(() => {
        getCovidData();
    }, []);

    return (
        <>
        <h2>Covid Stats In India</h2>

        <div className="main">
            <div className="card">
            India
            </div>
            <div className="card">
            Active<p>{sata.active}</p>
            </div>
            
            <div className="card">
            Confirmed<p>{sata.confirmed}</p>
            </div>
            <div className="card">
            Recovered<p>{sata.recovered}</p>
            </div>
            <div className="card">
            Deaths<p>{sata.deaths}</p>
            </div>
            
        </div>
        </>
    )
}
export default Covid
